import Header from "../../components/Header.vue";
import Footer from "../../components/Footer.vue";
import DataHeader from "../../components/DataHeader.vue";
import { News, newsComponent } from "../../../commons/js/news.js";
import { StringRenderer } from "../../../commons/js/string_renderer.js";
import { Utils } from "../../../commons/js/utils.js";
import { VistaGallerie } from "../../js/gallerie.js";
import { ConsiglioInforma } from "../../js/consiglioinforma";

export default {
    name: "app",
    components: {
        Footer,
        Header,
        DataHeader
    },
    data: function () {
        $(document).ready(renderHome);
        return {
            baseUrl: process.env.BASE_URL
        };
    }
};

const baseUrl = process.env.BASE_URL;

function renderHome() {
    renderNewsInEvidenza();
    renderNewsCompatte();
    renderGallerieVideo();
    renderGalleriaImmagini();
    renderApprofondimenti();
    renderRiviste();
    renderDiretta();
}

function renderDiretta() {
    $.ajax({
        url: apibase.baseurl + '/10535',
        success: function (result) {
            if (result != null && result.content.length > 0 &&
                result.content[0].properties.stato_della_diretta != 'OFFLINE') {
                $('#colonnaDiretta').removeClass('d-none');
            }
        }
    })
}

function renderNewsInEvidenza() {
    $.ajax({
        url: apibase.baseurl + '/10500' + '?size=2',
        method: 'POST',
        data: JSON.stringify({
            approfondimento: "F",
            "SORT": [{
                "direction": "DESC",
                "property": "data_pubblicazione"
            },{
                "direction": "DESC",
                "property": "data_pubblicazione_time"
            }]
        }),
        contentType: 'application/json',
        dataType: 'json',
        success: function (result) {
            if (result != null && result.content.length > 0) {
                var allNews = result.content;
                var newsHtml = [];
                allNews.forEach(function (item) {
                    let news = new News();
                    if (item.properties.url) {
                        news.link = {
                            url: item.properties.url,
                            target: '_blank'
                        }
                    } else {
                        news.link = {
                            url: 'notizia.html?id=' + item.id,
                            target: '_self'
                        }
                    }
                    news.dataPubblicazione = item.properties.data_pubblicazione;
                    news.oraPubblicazione = item.properties.data_pubblicazione_time.slice(0, 5);
                    news.estratto = item.properties.intestazione;
                    news.titolo = item.properties.title;
                    if (item.components.immagine && item.components.immagine.components.length) {
                        news.immagine = {
                            titolo: item.components.immagine.components[0].title,
                            src: baseImgUrl + item.components.immagine.components[0].components.file.components[0].properties.address
                        }
                        news.immagine.src = Utils.creaPlaceholder(news.immagine.src, '//via.placeholder.com/512x320');
                    }
                    newsHtml.push(newsComponent.renderNews(news));
                });
                var colonna1 = newsHtml.length ? '<div class="col-12 col-md-6">' + newsHtml[0] + '</div>' : '';
                var colonna2 = newsHtml.length > 1 ? '<div class="col-12 col-md-6">' + newsHtml[1] + '</div>' : '';
                $("#rigaNewsInEvidenza").html(colonna1 + colonna2);
            }
        }
    })
}

function renderNewsCompatte() {
    $.ajax({
        url: apibase.baseurl + '/10500' + '?size=10',
        method: 'POST',
        data: JSON.stringify({
            approfondimento: "F",
            "SORT": [{
                "direction": "DESC",
                "property": "data_pubblicazione"
            },{
                "direction": "DESC",
                "property": "data_pubblicazione_time"
            }]
        }),
        contentType: 'application/json',
        dataType: 'json',
        success: function (result) {
            if (result != null && result.content.length > 0) {
                var allNews = result.content;
                var newsHtml = [];
                if (allNews.length > 2) {
                    allNews.slice(2, allNews.length).forEach(function (item) {
                        let news = new News();
                        if (item.properties.url) {
                            news.link = {
                                url: item.properties.url,
                                target: '_blank'
                            }
                        } else {
                            news.link = {
                                url: 'notizia.html?id=' + item.id,
                                target: '_self'
                            }
                        }
                        news.dataPubblicazione = item.properties.data_pubblicazione;
                        news.oraPubblicazione = item.properties.data_pubblicazione_time.slice(0, 5);
                        news.titolo = item.properties.title;
                        newsHtml.push(newsComponent.renderNewsCompatta(news));
                    });
                    $("#rigaNews").html(newsHtml.join(''));
                }
            }
        }
    })
}

function renderGalleriaImmagini() {
    $.ajax({
        url: apibase.baseurl + '/10101' + '?size=1',
        method: 'POST',
        data: JSON.stringify({
            site_id: 204928,
            "SORT": [{
                "direction": "DESC",
                "property": "data_pubblicazione"
            }]
        }),
        contentType: 'application/json',
        dataType: 'json',
        success: function (result) {
            if (result != null && result.content.length > 0) {
                const galleria = result.content[0];
                let immagine = {};
                if (galleria.components.immagine && galleria.components.immagine.components.length) {
                    immagine = {
                        titolo: galleria.components.immagine.components[0].title,
                        src: baseImgUrl + galleria.components.immagine.components[0].components.file.components[0].properties.thumbaddress
                    }
                    immagine.src = Utils.creaPlaceholder(immagine.src, '//via.placeholder.com/512x320');
                }
                let linkOggetto = 'galleria-immagini.html?id=' + galleria.id;
                let linkArchivio = 'gallerie-immagini.html';
                let html = VistaGallerie.renderAnteprimaGalleria({
                    srcImmagine: immagine.src,
                    titoloImmagine: immagine.titolo,
                    titoloOggetto: galleria.title,
                    linkOggetto,
                    linkArchivio
                });
                $('#galleriaImmagini').html(html);
            }
        }
    });
}

function renderGallerieVideo() {
    $.ajax({
        url: apibase.baseurl + '/10536' + '?size=1',
        method: 'POST',
        data: JSON.stringify({
            "SORT": [{
                "direction": "DESC",
                "property": "date"
            }]
        }),
        contentType: 'application/json',
        dataType: 'json',
        success: function (result) {
            if (result != null && result.content.length > 0) {
                const galleria = result.content[0];
                let immagine = {};
                if (galleria.components.image && galleria.components.image.components.length) {
                    immagine = {
                        titolo: galleria.components.image.components[0].title,
                        src: Utils.creaUrlImmagine(galleria.components.image.components[0].properties.address)
                    }
                    immagine.src = Utils.creaPlaceholder(immagine.src, '//via.placeholder.com/512x320');
                }
                let linkOggetto = 'video.html?id=' + galleria.id;
                let linkArchivio = 'galleria-video.html';
                let html = VistaGallerie.renderAnteprimaGalleria({
                    srcImmagine: immagine.src,
                    titoloImmagine: immagine.titolo,
                    titoloOggetto: galleria.title,
                    linkOggetto,
                    linkArchivio
                });
                $('#galleriaVideo').html(html);
                renderGalleriaFocus(result.id);
            }
        }
    })
}

function renderGalleriaFocus(excludeId) {
    $.ajax({
        url: apibase.baseurl + '/10536' + '?size=2',
        method: 'POST',
        data: JSON.stringify({
            type: "FOCUS",
            "SORT": [{
                "direction": "DESC",
                "property": "date"
            }]
        }),
        contentType: 'application/json',
        dataType: 'json',
        success: function (result) {
            if (result != null && result.content.length > 0) {
                let galleria = result.content[0];
                if (galleria.id == excludeId && result.content.length > 1) {
                    galleria = result.content[1];
                }
                let immagine = {};
                if (galleria.components.image && galleria.components.image.components.length) {
                    immagine = {
                        titolo: galleria.components.image.components[0].title,
                        src: Utils.creaUrlImmagine(galleria.components.image.components[0].properties.address)
                    }
                    immagine.src = Utils.creaPlaceholder(immagine.src, '//via.placeholder.com/512x320');
                }
                let linkOggetto = 'video.html?id=' + galleria.id;
                let linkArchivio = 'galleria-video.html?tipo=FOCUS';
                let html = VistaGallerie.renderAnteprimaGalleria({
                    srcImmagine: immagine.src,
                    titoloImmagine: immagine.titolo,
                    titoloOggetto: galleria.title,
                    linkOggetto,
                    linkArchivio
                });
                $('#galleriaVideoFocus').html(html);
            }
        }
    })
}



function renderApprofondimenti() {
    $.ajax({
        url: apibase.baseurl + '/10500' + '?size=4',
        method: 'POST',
        data: JSON.stringify({
            approfondimento: "T",
            "SORT": [{
                "direction": "DESC",
                "property": "data_pubblicazione"
            },{
                "direction": "DESC",
                "property": "data_pubblicazione_time"
            }]
        }),
        contentType: 'application/json',
        dataType: 'json',
        success: function (result) {
            if (result && result.content && result.content.length) {
                let htmlApprofondimenti = '';
                result.content.forEach(function (news) {
                    let immagine = {};
                    var address = "";
                    var htmlImg = "";
                    if (news.components && news.components.immagine) {
                        var imgSrc = '';
                        if (news.components.immagine.components.length > 0) {
                            immagine = news.components.immagine.components[0];
                            address = immagine.components.file.components[0].properties.thumbaddress;
                            imgSrc = baseImgUrl + address;
                        }
                        imgSrc = Utils.creaPlaceholder(imgSrc, '//via.placeholder.com/512x320');
                        htmlImg = '<img style="height: 150px; width: auto" src="' + imgSrc + '" />'
                    }
                    var linkNews = news.properties.url;
                    var target = '_blank';
                    if (!linkNews) {
                        linkNews = 'notizia.html?id=' + news.id;
                        target = '_self';
                    }
                    var stringedDate = "";
                    if (news.properties.data_pubblicazione != undefined) {
                        stringedDate = StringRenderer.renderDataTestuale(news.properties.data_pubblicazione)
                    } else if (news.lastModifiedDate) {
                        stringedDate = StringRenderer.renderDataTestuale(news.lastModifiedDate)
                    }
                    htmlApprofondimenti += '\
                    <div class="item p-2">\
                        <div>' + htmlImg + ' </div>\
                        <div class="medium-font-size">\
                            <hr class="border-news pr-2 pl-2"/>\
                            <p class="text-font mt-2 mb-0">' + stringedDate + '</p>\
                            <h6 >' + news.title + '</h6>\
                            <p class="text-font">' + news.properties.intestazione + '</p>\
                            <a class="read-more" target="' + target + '" href="' + linkNews + '">\
                                <span class="text">Leggi di più</span>\
                                <svg class="icon"><use xlink:href="' + baseUrl + 'svg/sprite.svg#it-arrow-right"></use></svg>\
                            </a>\
                        </div>\
                    </div>';
                });
                $('#approfondimenti').html('<div id="sliderApprofondimenti" class="owl-carousel">' + htmlApprofondimenti + '</div>');
                var owl = $("#sliderApprofondimenti");
                owl.owlCarousel({
                    responsive: {
                        0: {
                            items: 1
                        },
                        576: {
                            items: 2
                        },
                        768: {
                            items: 3
                        },
                        992: {
                            items: 4
                        }
                    },
                });
            }
        }
    });
}

function renderRiviste() {
    const rivisteDaCaricare = ConsiglioInforma.Riviste;
    const promises = [];
    Object.entries(rivisteDaCaricare).slice(0,3).forEach(function (rivista, index) {
        let idTipologia = rivista[0];
        let tipologia = rivista[1].enum;
        promises.push(new Promise(function (success, done) {
            $.ajax({
                url: apibase.baseurl + '/10530' + '?size=1',
                method: 'POST',
                data: JSON.stringify({
                    tipologia
                }),
                contentType: 'application/json',
                dataType: 'json',
                success: function (result) {
                    if (result && result.content && result.content.length) {
                        let htmlRiviste = renderRivista(result.content[0], idTipologia);
                        $('[data-rivista]').get(index).innerHTML = htmlRiviste;
                    }
                    success();
                },
                error: done
            });
        }));
    });
    Promise.all(promises).then(function () {
        var owl = $("#sliderRiviste");
        owl.owlCarousel({
            responsive: {
                0: {
                    items: 1
                },
                768: {
                    items: 2
                },
                992: {
                    items: 3
                }
            },
        });
    });
}

function renderRivista(rivista, idTipologia) {
    let immagine = {};
    var address = "";
    var htmlImg = "";
    if (rivista.components && rivista.components.immagine) {
        var imgSrc = '';
        if (rivista.components.immagine.components.length > 0) {
            immagine = rivista.components.immagine.components[0];
            address = immagine.components.file.components[0].properties.address;
            imgSrc = baseImgUrl + address;
        }
        imgSrc = Utils.creaPlaceholder(imgSrc, '//via.placeholder.com/320x512');
        htmlImg = '<img class="img-fluid" src="' + imgSrc + '" />'
    }
    var linkRivista = rivista.properties.url;
    var target = '_blank';
    if (!linkRivista) {
        linkRivista = 'rivista.html?id=' + rivista.id;
        target = '_self';
    }
    return '\
    <div class="container-fluid">\
        <div class="row">\
            <div class="col">' + htmlImg + ' </div>\
            <div class="col medium-font-size">\
                <h6 >' + rivista.title + '</h6>\
                <a class="read-more" target="' + target + '" href="' + linkRivista + '">\
                    <span class="text">Leggi di più</span>\
                    <svg class="icon"><use xlink:href="'+ baseUrl + 'svg/sprite.svg#it-arrow-right"></use></svg>\
                </a>\
            </div>\
        </div>\
        <div class="p-4"><a class="btn bottone-con-freccia bottom-right text-uppercase" href="anni-rivista.html?rivista=' + idTipologia + '">Archivio</a></div>\
    </div > ';
}

$("a.smooth-scroll").on("click", function (event) {
    var $anchor = $(this);
    $("html, body")
        .stop()
        .animate(
            {
                scrollTop: $($anchor.attr("href")).offset().top + 20
            },
            1500,
            "easeInOutExpo"
        );
    event.preventDefault();
});